.catalog {
  padding-top: 109px;
  padding-right: 25px;
  padding-bottom: 90px;
  padding-left: 25px;
  color: $light;
}

.catalog__list {
  display: flex;
  flex-wrap: wrap;

  @include list-reset;
}

.catalog__hover {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: space-between;
  flex-grow: 1;
  visibility: hidden;
}

.catalog__item {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: space-between;
  width: 23%;
  min-height: 386px;
  margin-right: 2.65%;
  margin-bottom: 30px;
  padding-top: 29px;
  padding-right: 25px;
  padding-bottom: 8px;
  padding-left: 30px;
  background-color: $dark-blue;

  &:nth-of-type(4n) {
    margin-right: 0;
  }

  &:hover,
  &:focus {
    padding-bottom: 35px;
    padding-left: 25px;
    background: $dark-blue;

    .catalog__hover {
      visibility: visible;
    }

    h3 {
      order: -1;
    }
  }
}

.catalog__item--astoria {
  background: url("../img/astoria-desktop@1x.jpg") no-repeat top center;
}

@media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
  .catalog__item--astoria {
    background-image: url("../img/astoria-desktop@2x.jpg");
    background-size: cover;
  }
}

.catalog__item--atlantis {
  background: url("../img/atlantis-desktop@1x.jpg") no-repeat top center;
}

@media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
  .catalog__item--atlantis {
    background-image: url("../img/atlantis-desktop@2x.jpg");
    background-size: cover;
