    background-image: url("../img/minion-desktop@2x.jpg");
    background-size: cover;
  }
}

.catalog h2 {
  margin: 0;
  margin-bottom: 48px;

  @include title-desktop;
  text-align: center;
}

.catalog h3 {
  order: 2;
  margin: 0;
  margin-bottom: 14px;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
}

.catalog h4 {
  margin: 0;
  margin-bottom: 1px;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
}

.catalog p {
  margin: 0;
  margin-bottom: 11px;
  font-size: 14px;
  line-height: 20px;
}

.catalog a {
  @include button;

  &:hover,
  &:focus {
    color: rgba($dark-blue, 0.8%);
  }

  &:active {
    color: rgba($dark-blue, 0.5%);
  }
}

